export default {
	install(Vue) {
		Vue.mixin({
			data() {
				return {
					telNumber: /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/,
					userRules: {
						loginName: {
							rules: [{
									required: true,
									'errorMessage': ""
								},
								{
									validateFunction: (rule, val, data, callback) => {
										switch (true) {
											case val.length < 6:
												callback('')
												break;
											default:
												return true
										}
									}
								} // 
							]
						},
						password: {
							rules: [{
									required: true,
									'errorMessage': ''
								},
								{
									validateFunction: (rule, val, data, callback) => {
										switch (true) {
											case val.length < 6:
												callback('')
												break;
											default:
												return true
										}
									}
								}
							]
						},
						phone: {
							rules: [{
									required: true,
									'errorMessage': ''
								},
								{
									validateFunction: (rule, val, data, callback) => {
										switch (true) {
											case !this.telNumber.test(val):
												callback('')
												break;
											default:
												return true
										}
									}
								}
							]
						},
						vCode: {
							rules: [{
									required: true,
									'errorMessage': ''
								},
								{
									validateFunction: (rule, val, data, callback) => {
										switch (true) {
											case val.length < 4:
												callback('')
												break;
											default:
												return true
										}
									}
								}, {
									validateFunction: (rule, val, data, callback) => {
										switch (true) {
											case val != this.ruleCode:
												callback('')
												break;
											default:
												return true
										}
									}
								}
							]
						}
					}
				}
			}
		})
	}
}
